'use client'

import { useState } from 'react'
import Image from 'next/image'
import Link from 'next/link'
import { Heart, Play } from 'lucide-react'
import { Anime } from '@/lib/api'

interface AnimeCardProps {
  anime: Anime
}

export function AnimeCard({ anime }: AnimeCardProps) {
  const [isFavorite, setIsFavorite] = useState(false)

  return (
    <Link href={`/anime/${anime.mal_id}`}>
      <div className="group cursor-pointer relative h-full">
        {/* Card Container */}
        <div className="relative overflow-hidden rounded-lg subtle-hover bg-card h-96">
          {/* Image */}
          <Image
            src={anime.images.jpg.large_image_url || "/placeholder.svg"}
            alt={anime.title}
            fill
            className="object-cover group-hover:scale-105 transition-transform duration-300"
          />

          {/* Gradient Overlay */}
          <div className="absolute inset-0 bg-gradient-to-t from-black via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity" />

          {/* Play Button Overlay */}
          <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity">
            <div className="w-14 h-14 bg-secondary rounded-full flex items-center justify-center">
              <Play size={24} className="text-foreground fill-foreground" />
            </div>
          </div>

          {/* Score Badge */}
          <div className="absolute top-2 right-2 bg-secondary px-3 py-1 rounded-full text-sm font-bold text-secondary-foreground">
            {anime.score.toFixed(1)}
          </div>

          {/* Info */}
          <div className="absolute bottom-0 left-0 right-0 p-3 text-white opacity-0 group-hover:opacity-100 transition-opacity">
            <h3 className="font-semibold text-sm line-clamp-2 mb-1">{anime.title}</h3>
            <p className="text-xs text-muted-foreground line-clamp-1">
              {anime.episodes ? `${anime.episodes} Episodes` : 'TBA'}
            </p>
          </div>
        </div>

        {/* Favorite Button */}
        <button
          onClick={(e) => {
            e.preventDefault()
            setIsFavorite(!isFavorite)
          }}
          className="absolute -bottom-2 right-2 z-10 p-2 bg-card rounded-full border border-border hover:bg-secondary hover:text-secondary-foreground transition-colors"
        >
          <Heart
            size={18}
            className={isFavorite ? 'fill-current text-accent' : 'text-foreground'}
          />
        </button>

        {/* Title Below Card */}
        <h3 className="mt-3 font-semibold text-sm line-clamp-2 group-hover:text-accent transition-colors">
          {anime.title}
        </h3>
        <p className="text-xs text-muted-foreground mt-1">
          {anime.genres?.slice(0, 2).map(g => g.name).join(', ')}
        </p>
      </div>
    </Link>
  )
}
